.App {
	text-align: center;
}



.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

body {
	margin: 0;
	font-family: 'Montserrat', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  }

  

  h1 {
	font-size:2.5rem;
	border-bottom: solid black 1px;
	display:inline-block;
	font-weight: 500;
	margin: 1rem;
  }

  h2 {
	font-size:1.5rem;
	font-weight:500;
	margin: 1rem;
  }

  h3 {
	font-size:1.17rem;
	margin: 1rem;
  }

  h4 {
	font-size:1rem;
	margin: 1rem;
  }

  p {
	font-size:1.2rem;
	font-weight: 400;
	margin: 1rem;
  }

  span {
	font-size:1.4rem;
	font-weight: 400;	
  }